import React from 'react'
import { Link } from 'gatsby'

import styles from './Header.module.scss'

import Menu from './Menu'
import Widget from '../../common/Widgets/MainWidget'
import helpers from '../../../helpers'

import logoText from '../../../images/logoText.svg'
import notificationsImg from '../../../images/notifications.svg'

const { linksPaths } = helpers.constants.links


const Header = ({ location }) => {
    const pathname = location ? location.pathname : '/404/'
    
    return (
        <React.Fragment>
            <Menu />
            <header className={styles.header}>
                <div className={styles.header__container}>
                    <div className={styles.main}>

                        {
                            pathname.includes('/service/')
                                ? ''
                                : (
                                    <Widget location={location} />
                                )
                        }

                        <div className={styles.notifications}>
                            <img src={notificationsImg} alt="notifications" />
                        </div>

                        <div className={styles.logo}>
                            <Link to={linksPaths.briefcase}>
                                <img src={logoText} alt="logo" />
                            </Link>
                        </div>
                    </div>

                </div>
            </header>
        </React.Fragment>
    )
}

export default Header
