import React from 'react'
import Loadable from 'react-loadable'

import axios from 'axios'
import helpers from '../../../helpers'

import styles from './Currencyes.module.scss'

import Loader from '../Loader'


const LoadableGraphUsd = Loadable.Map({
    loader: {
        Graph: () => import('./Graph'),
        data: () => axios.get(helpers.api.GET_USD, { withCredentials: true }).then(res => ({
            result: res.data.result,
        })),
    },
    render(loaded, props) {
        const Graph = loaded.Graph.default
        const { data } = loaded
        return <Graph {...props} data={data} />
    },
    loading() {
        return <Loader />
    },
})

const LoadableGraphEuro = Loadable.Map({
    loader: {
        Graph: () => import('./Graph'),
        data: () => axios.get(helpers.api.GET_EURO, { withCredentials: true }).then(res => ({
            result: res.data.result,
        })),
    },
    render(loaded, props) {
        const Graph = loaded.Graph.default
        const { data } = loaded
        return <Graph {...props} data={data} />
    },
    loading() {
        return <Loader />
    },
})

const myGraph = (props) => {
    switch (props.name) {
        case 'usd':
            return <LoadableGraphUsd suffix={props.name} {...props} />
        case 'euro':
            return <LoadableGraphEuro suffix={props.name} {...props} />

        default:
            return <LoadableGraphUsd suffix={props.name} {...props} />
    }
}

const GraphComponent = props => (
    <div className={styles.currency}>
        {
            myGraph(props)
        }
    </div>
)

export default GraphComponent
