import React from 'react'

import Layout from '../../components/layouts/Layout'

import SEO from '../../components/Seo'

import Currencyes from '../../components/common/Currencyes'
import MainWidget from '../../components/common/Widgets/MainWidget'

const UsdRubGraphPage = ({ location }) => (
    <Layout location={location}>
        <SEO title="Портфель" keywords={['gatsby', 'application', 'react']} />
        <Currencyes name="euro" />
        <MainWidget location={location} mobile />
    </Layout>
)

export default UsdRubGraphPage
