import React from 'react'
import Loadable from 'react-loadable'
import axios from 'axios'

import helpers from '../../../../helpers'

const { GET_ORDERS, GET_TRADES, GET_ORDERS_INFO } = helpers.api

const LoadableMainWidget = Loadable.Map({
    loader: {
        MainWidget: () => import('./MainWidget'),
        WidgetInfo: () => axios.get(GET_ORDERS, { withCredentials: true }).then(resOrders =>
            axios.get(GET_TRADES, { withCredentials: true }).then(resTrades =>
                axios.get(GET_ORDERS_INFO, { withCredentials: true }).then(resInfo => ({
                    info: {
                        orders_count: resOrders.data.count,
                        trades_count: resTrades.data.count,
                        orders_done_count: resInfo.data.countDone,
                        orders_undone_count: resInfo.data.countAll - resInfo.data.countDone,
                        loaded: true,
                    },
                })))),
    },
    render(loaded, props) {
        const MainWidget = loaded.MainWidget.default
        const { WidgetInfo } = loaded
        return <MainWidget loaded {...props} {...WidgetInfo} />
    },
    loading() {
        return ''
    },
})

const MainWidget = props => (
    <LoadableMainWidget {...props} />
)

export default MainWidget
